import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

connect.send("VKWebAppInit", {});

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() { 
		connect.subscribe((k) => { 
		   switch (k.detail.type) { 
		case 'VKWebAppShowWallPostBox': 
			  this.setState({ hm: k.detail.data }); 
		   break; 
				 default: 
		console.log(k.detail.type); 
   } 
 }); 
 connect.send("VKWebAppShowWallPostBox", {"message": "Я пользуюсь крутым фонариком не выходя из ВКонтакте, и тебе советую! \n Ссылка:https://vk.com/app6951202 "});
}
	
componentDidMount() { 
	connect.subscribe((k) => { 
	   switch (k.detail.type) { 
	case 'VKWebAppFlashGetInfoResult': 
		  this.setState({ hm: k.detail.data }); 
	   break; 
			 default: 
	console.log(k.detail.type); 
} 
}); 
connect.send("VKWebAppFlashGetInfo", {});
}
	
	
	





        componentDidMount() { 
             connect.subscribe((k) => { 
                switch (k.detail.type) { 
             case 'VKWebAppFlashSetLevelResult': 
                   this.setState({ hm: k.detail.data }); 
                break; 
                      default: 
             console.log(k.detail.type); 
        } 
      }); 
	  connect.send("VKWebAppFlashSetLevel", {"level": 0});
     }


	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Persik id="persik" go={this.go} />
			</View>
		);
	}
}

export default App;
